import Vue from 'vue'
import Moment from 'moment'
import { Toast, PullRefresh, Dialog } from 'vant'
import App from './App'
import router from './router'
import store from './store'

Moment.locale('zh-cn')
Vue.config.productionTip = false
Vue.filter('time', (v) => {
  return Moment(v).fromNow()
})
Vue.use(Toast)
Vue.use(PullRefresh)
Vue.use(Dialog)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})



