import Vue from 'vue'
import Router from 'vue-router'

const Root = () => import('@/page/Root')
const Home = () => import('@/page/Home')
const Categroy = () => import('@/page/Categroy')
const Message = () => import('@/page/Message')
const Me = () => import('@/page/Me')
const Topic = () => import('@/page/Topic')
const Login = () => import('@/page/Login')
const User = () => import('@/page/User')
const Collect = () => import('@/page/Collect')

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior (to, from, saved) {
    if (to.hash) {
      return {selector: to.hash}
    }
  },
  routes: [
    {
      path: '/',
      component: Root,
      children: [
        {
          path: '/',
          name: 'Home',
          component: Home
        },
        {
          path: '/category',
          name: 'Categroy',
          component: Categroy
        },
        {
          path: '/message',
          name: 'Message',
          component: Message
        },
        {
          path: '/me',
          name: 'Me',
          component: Me
        }
      ]
    },
    {
      path: '/topic/:id',
      name: 'Topic',
      component: Topic
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user/:name',
      name: 'User',
      component: User
    },
    {
      path: '/user/:name/collect',
      name: 'Collect',
      component: Collect
    }
  ]
})



