import Vue from 'vue'
import Store from 'store'
import Vuex from 'vuex'

import { postToken } from '../util/api'
import { CODE } from '../config'
// 初始化 addLogo
Store.set('addLogo', true)

Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    accesstoken: Store.get('token'),
    user: Store.get('user'),
    addLogo: Store.get('addLogo')
  },
  mutations: {
    setUser (state, user) {
      state.user = user
      Store.set('user', user)
    },
    setToken (state, token) {
      state.accesstoken = token
      Store.set('token', token)
    },
    setAddLogo (state, addLogo) {
      state.addLogo = addLogo
      Store.set('addLogo', addLogo)
    }
  },
  getters: {
    token (state) {
      return state.accesstoken ? state.accesstoken : ''
    },
    user (state) {
      if (state.user) {
        return state.user
      } else {
        return null
      }
    },
    addLogo (state) {
      return state.addLogo
    }
  },
  actions: {
    async logIn ({ commit, state }, token) {
      try {
        const res = await postToken({accesstoken: token})
        if (res.success) {
          commit('setUser', res)
          commit('setToken', token)
          return {
            code: CODE.SUCCESS,
            msg: '登陆成功!'
          }
        }
        return {
          code: CODE.FAIL,
          msg: res.error_msg
        }
      } catch (error) {
        console.error(error)
        return {
          code: CODE.FAIL,
          msg: '网络有问题?'
        }
      }
    },
    async logOut ({commit}) {
      commit('setUser', null)
      commit('setToken', null)
    }
  }
})



